<template>
	<view class="page bgwhite">
		<view class="list">
			<u--form ref="uform" :model="form" :rules="rules" label-width="110px">
				<u-form-item :label="$t('工单号')" style="padding-left: 10px;" prop="orderNo">
					<u--input v-model="form.orderNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('机器编号')" style="padding-left: 10px;" prop="machineNo">
					<u--input v-model="form.machineNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('模具编号')" style="padding-left: 10px;" prop="mouldNo">
					<u--input v-model="form.mouldNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('产品编号')" style="padding-left: 10px;" prop="itemNo">
					<u--input v-model="form.itemNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('是否完成')" style="padding-left: 10px;" prop="finishStatus">
					<view class="itemView"><u--input v-model="form.finishStatus" :placeholder="$t('please.select')"
							border="surround" suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
							@focus="finishShow = true"></u--input>
						<u-picker :show="finishShow" :columns="finishs" @cancel="finishShow = false" @confirm="finishConfirm"
							@change="finishChangeHandler" :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-picker>
					</view>
				</u-form-item>
				<u-form-item :label="$t('是否超量完成')" style="padding-left: 10px;" prop="overFinishStatus">
					<view class="itemView"><u--input v-model="form.overFinishStatus" :placeholder="$t('please.select')"
							border="surround" suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
							@focus="overFinishShow = true"></u--input>
						<u-picker :show="overFinishShow" :columns="finishs" @cancel="overFinishShow = false"
							@confirm="overFinishConfirm" @change="overFinshChangeHandler" :cancel-text="$t('取消')"
							:confirm-text="$t('确认')"></u-picker>
					</view>
				</u-form-item>


				<u-form-item :label="$t('派工状态')" style="padding-left: 10px;" prop="status">
					<view class="itemView"><u--input v-model="form.status" :placeholder="$t('please.select')"
							border="surround" suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
							@focus="statusShow = true"></u--input>
						<u-picker :show="statusShow" :columns="status" @cancel="statusShow = false" @confirm="statusConfirm"
							@change="statusChangeHandler" :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-picker>
					</view>
				</u-form-item>
				<u-form-item :label="$t('计划开始时间')" style="padding-left: 10px;" prop="palnStartTime">
					<view class="itemView">
						<u--input v-model="form.planStartTime" :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
							@focus="planStartTimeShow = true"></u--input>
						<u-datetime-picker :show="planStartTimeShow" v-model="valueDate" mode="datetime"
							:formatter="formatter" @cancel="planStartTimeShow = false" @confirm="planStartTimeConfirm"
							@change="planStartChangeHandler" :cancel-text="$t('取消')"
							:confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>
				<u-form-item :label="$t('计划结束时间')" style="padding-left: 10px;" prop="palnEndTime">
					<view class="itemView">
						<u--input v-model="form.planEndTime" :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
							@focus="planEndTimeShow = true"></u--input>
						<u-datetime-picker :show="planEndTimeShow" v-model="valueDate" mode="datetime"
							:formatter="formatter" @cancel="planEndTimeShow = false" @confirm="planEndTimeConfirm"
							@change="planEndChangeHandler" :cancel-text="$t('取消')"
							:confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>

				<u-form-item :label="$t('完成时间')" style="padding-left: 10px;" prop="actualEndTime">
					<view class="itemView">
						<u--input v-model="form.actualEndTime" :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
							@focus="finishTimeShow = true"></u--input>
						<u-datetime-picker :show="finishTimeShow" v-model="valueDate" mode="datetime" :formatter="formatter"
							@cancel="finishTimeShow = false" @confirm="endTimeConfirm" @change="endTimeChangeHandler"
							:cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>
				<u-form-item :label="$t('换单时间')" style="width: 100%;padding-left: 10px;" prop="actualStartTime">
					<view class="itemView">
						<u--input v-model="form.actualStartTime" :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399"
							style="height: 20px;margin-right: 10px;" @focus="changeOrderTimeShow = true"></u--input>
						<u-datetime-picker :show="changeOrderTimeShow" v-model="valueDate" mode="datetime"
							:formatter="formatter" @cancel="changeOrderTimeShow = false" @confirm="changeOrderConfirm"
							@change="changeOrderChangeHandler" :cancel-text="$t('取消')"
							:confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>

			</u--form>
		</view>

		<u-button class="confirmBtn" shape="circle" :text="$t('screening')" @click="submit"></u-button>
	</view>
</template>

<script>
import pApi from "@/api/public/index.js"
export default {
	filters: {
		formatDate(time) {
			var data = new Date(time);
			return formatDate(data, 'yyyy MM dd hh:mm:ss');
		}
	},
	onLoad() {
		this.getDispatchOrderStatus()
	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("order.monitoring")
		});
	},
	data() {
		return {
			form: {
				orderNo: null,
				machineNo: null,
				mouldNo: null,
				itemNo: null,
				status: null,
				dispatchStatus: null,
				planStartTime: null,
				planEndTime: null,
				actualEndTime: null,
				actualStartTime: null,
				finishStatus: null,
				overFinishStatus: null,
				complete: null,
				overComplete: null,
			},
			rules: {},
			finishShow: false,
			excessiveFinishShow: false,
			statusShow: false,
			finishShow: false,
			overFinishShow: false,
			planStartTimeShow: false,
			planEndTimeShow: false,
			finishTimeShow: false,
			changeOrderTimeShow: false,
			selectFinishIndex: 0,
			selectExcessiveIndex: 0,
			selectStatusIndex: 0,
			selectFinishIndex: 0,
			selectOverFinishIndex: 0,
			planStartTimeValue: null,
			planEndTimeValue: null,
			startTimeValue: null,
			endTimeValue: null,
			valueDate: Number(new Date()),
			finishs: [
				['是', '否']
			],
			status: [],
			finishStatus: null,
			overFinishStatus: null

		};
	},
	methods: {
		//获取派工单状态业务表
		getDispatchOrderStatus() {
			uni.$u.http.get(pApi.dispatchOrderStatus).then(
				result => {
					console.log('result', result);
					if (result.code === 200) {
						let statusList = []
						result.data.forEach(item => {
							statusList.push(this.$t(item.name))
						})
						if (statusList.length > 0) {
							this.status = []
							this.status.push(statusList)
						}
					}
				}).catch(er => {
					console.log('er', er);
				})
		},
		submit() {
			this.$refs.uform.validate(valid => {
				if (valid) {
					console.log('验证通过');
				} else {
					console.log('验证失败');
				}
			});

			uni.$emit('returnOrderData', this.form)
			uni.navigateBack({
				delta: 1
			});
		},
		statusChangeHandler(e) {
			this.selectStatusIndex = e.index
		},
		statusConfirm() {
			this.statusShow = false
			this.form.dispatchStatus = this.selectStatusIndex
			this.form.status = this.status[0][this.selectStatusIndex]
		},
		planStartChangeHandler(e) {
			console.log("选择的时间=", e.value)
			this.planStartTime = e.value
		},
		planStartTimeConfirm() {
			this.planStartTimeShow = false
			this.form.planStartTime = this.timestampToTime(this.planStartTime ? this.planStartTime : this.valueDate)
			this.handleChangeUpdateTime(true)
		},
		planEndChangeHandler(e) {
			console.log("选择的时间=", e.value)
			this.planEndTime = e.value
		},
		planEndTimeConfirm() {
			this.planEndTimeShow = false
			this.form.planEndTime = this.timestampToTime(this.planEndTime ? this.planEndTime : this.valueDate)
			this.handleChangeUpdateTime(true)
		},
		endTimeChangeHandler(e) {
			console.log("选择的时间=", e.value)
			this.finishTime = e.value
		},
		endTimeConfirm() {
			this.finishTimeShow = false
			this.form.actualEndTime = this.timestampToTime(this.finishTime ? this.finishTime : this
				.valueDate)
			this.handleChangeUpdateTime(false)
		},
		changeOrderChangeHandler(e) {
			console.log("选择的时间=", e.value)
			this.changeOrderTime = e.value
		},
		changeOrderConfirm() {
			this.changeOrderTimeShow = false
			this.form.actualStartTime = this.timestampToTime(this.changeOrderTime ? this.changeOrderTime : this.valueDate);
			this.handleChangeUpdateTime(false)
		},

		finishConfirm() {
			this.finishShow = false
			this.form.finishStatus = this.finishs[0][this.selectFinishIndex]
			this.form.complete = this.form.finishStatus === "是" ? "0":"1";
		},
		finishChangeHandler(e) {
			this.selectFinishIndex = e.index
		},
		overFinishConfirm() {
			this.overFinishShow = false
			this.form.overFinishStatus = this.finishs[0][this.selectOverFinishIndex]
			this.form.overComplete = this.form.overFinishStatus === "是" ? "0":"1";
		},
		overFinshChangeHandler(e) {
			this.selectOverFinishIndex = e.index
		},

		//时间戳转时间
		timestampToTime(value) {
			let date = new Date(value);
			let y = date.getFullYear(); // 年
			let MM = date.getMonth() + 1; // 月
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate(); // 日
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours(); // 时
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes(); // 分
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds(); // 秒
			s = s < 10 ? ('0' + s) : s;
			return y + '-' + MM + '-' + d + " " + h + ':' + m + ':' + s;
		},

		formatter(type, value) {
			if (type === 'year') {
				return `${value}年`
			}
			if (type === 'month') {
				return `${value}月`
			}
			if (type === 'day') {
				return `${value}日`
			}
			if (type === 'hour') {
				return `${value}时`
			}
			if (type === 'minute') {
				return `${value}分`
			}
			return value
		},

		handleChangeUpdateTime(isPlan) {
			// 开始时间
			var start = (new Date(isPlan ? this.form.planStartTime : this.form.actualStartTime) * 1000) / 1000;

			// 结束时间
			var end = (new Date(isPlan ? this.form.planEndTime : this.form.actualEndTime) * 1000) / 1000;

			// 结束时间小于开始时间
			if (start !== 0 && end !== 0) {
				if (start > end) {
					if (isPlan) {
						this.form.planEndTime = this.form.planStartTime;
						uni.$u.toast("计划结束时间小于计划开始时间！")
					} else {
						this.form.actualEndTime = this.form.actualStartTime;
						uni.$u.toast("换单时间小于完成时间！")
					}

				}
			}
		},

	}
}
</script>

<style lang="scss" scoped>
.list {
	&-item {
		padding: 10upx 16upx;
		@include after();
		font-size: 28upx;
		border: 0;
		justify-content: center;
		align-items: center;

		.label {
			width: 200upx;
			color: #333;
			font-weight: 500;
		}

		.value {
			color: #666;
		}
	}
}

.itemView {
	width: 100%;
	margin-right: 10px;
}

.confirmBtn {
	width: 80%;
	margin-top: 20px;
	margin-bottom: 20px;
	align-items: center;
	color: white;
	font-size: 20px;
	background-color: $ch-theme;
}
</style>